    margin: 0;
    padding: 0;

    height: 100%
}

#page {
    min-height: 100%;
    display: flex;
}

.page-header {

    height: 5rem;
    display: flex;
    align-items: center;
}

.page-header button.home {

    width: 4rem;
    height: 4rem;

}
.page-title {
    font-size: 4rem;
    font-family: 'Script', Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

#nav {
    min-width: clamp(40ch, 20%, 60ch);
    background: var(--color-theme-alt);

    display: flex;
    flex-direction: column;
    padding: 1rem;

    border-right: 0.2rem outset var(--color-theme-alt-highlight);
}

@media (max-width: 800px) {
    #page {
        flex-direction: column;
    }
    #nav {
        min-width: 0;
    }
}

#content {

    flex-grow: 1;
    background: var(--color-theme-main-bg);

    display: flex;
    flex-direction: column;

    padding: 1rem;
}

.project button:hover, .project button:active {

    background-color: var(--color-theme-main-highlight);

}

.project {

    display: grid;
    grid-template-rows: min-content 1fr;
    gap: 2rem;
}

.input-wrapper {
    display: grid;
}

.input-wrapper::after {
    content: attr(data-copied-value) " ";
    white-space: pre-wrap;
    word-break: break-all;
    visibility: hidden;
}

.input-wrapper textarea {
    resize: none;
    overflow: hidden;
}

.input-wrapper textarea, .input-wrapper::after {

    padding: 0.5rem;

    grid-area: 1 / 1 / 2 / 2;

}
.project .header {

    background-color: var(--color-theme-main);
    padding: 1rem;
    border-radius: 0.5rem;

    display: grid;
    grid-template-columns: 1fr 2rem;
    grid-template-rows: min-content min-content min-content;

    .title {

        grid-column: 1 / 2;

    }
    .description {
        
        grid-column: 1 / 2;
        grid-row: 2 / -1;

    }

    button.new {

        min-height: 2rem;
        min-width: 2rem;
        grid-row: 3 / -1;
        border-radius: 0.5rem;

    }


}

@media (max-width: 800px) {

    .project .header {

        

    }

}

.todo-list {

}

.todo {

    background-color: var(--color-theme-main);
    border-radius: 0.5rem;
    padding: 1rem;
    margin-bottom: 1rem;

    display: grid;
    grid-template-columns: 1.5rem 1fr 2rem 2rem 2rem;
    align-items: center;
    column-gap: 0.5rem;
    row-gap: 1rem;

}

.todo.complete .title {

    text-decoration: line-through;
    color: var(--color-theme-alt-text);

}

.todo input[type='checkbox'] {

    width: 1.2rem;
    height: 1.2rem;
    cursor: pointer;
    border: 0.2rem outset var(--color-theme-alt-text);
    border-radius: 50%;
    padding: 0.1rem;

    transition: all 0.1s linear;
}

.todo input[type='checkbox']:checked {

    background-color: var(--color-theme-user);
    border: 0.5rem outset var(--color-theme-alt-text);

}

.todo .mini {
    grid-column: 2 / -1;
    display: grid;
    grid-template-columns: subgrid;
}

.todo .description {

    grid-column: 2 / 3;
    
}

.todo button {

    border-radius: 10%;
    height: 2rem;
    width: 2rem;
    
}

.project-li {
    height: 3rem;

    display: grid;
    grid-template-columns: 1fr 5ch 2rem;
    gap: 1rem;
    align-items: center;
    justify-items: left;

    border-radius: 0.1rem;
    padding-left: 1rem;
    padding-right: 1rem;

    border-bottom: 0.1rem solid var(--color-theme-alt-text);
}

.project-li:hover, .project-li:active {

    background-color: var(--color-theme-alt-highlight);

}

.project-li.new .icon {
    background-position: left;
}

.project-li .title {
    
    height: 100%;
    text-align: left;
    min-width: 20ch;
    max-width: 12vw;

    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;

}

@media (max-width: 800px) {

    .project-li .title {
        
        min-width: 0;
        max-width: 100%;
    
    }

}

.project-li .status {

    padding: 0.5rem;
    border-radius: 0.5rem;
    
}

.project-li .status.incomplete {

    background-color: var(--color-theme-negative);        

}

.project-li .status.in-progress {

    background-color: var(--color-theme-mixed);

}

.project-li .status.complete {

    background-color: var(--color-theme-positive);

}

.project-li button.delete {

    border-radius: 10%;
    height: 2rem;
    width: 2rem;
    
}

button.delete:hover, button.delete:active {
    background-color: var(--color-theme-negative);
}

